@import url("https://fonts.googleapis.com/css2?family=Signika+Negative:wght@300;400;600&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #8d8f18;
  --color-bg-variant: #5ba8db;
  --color-white: #d7db0f;
  --color-primary: #db2a25;
  --color-light: #8f0703;

  --contianer-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

  --o-transition: 0.4ms ease;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Signika Negative", sans-serif;
  background: var(--color-bg-variant);
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(../src/assets/background2.png);
}
.container {
  width: var(--contianer-width-lg);
  margin: auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h5 {
  color: var(--color-white);
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
  height: 100vh;
}

section > h2,
section > h5 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}
.text-light {
  color: var(--color-light);
}
a {
  color: var(--color-primary);
  transition: var(--o-transition);
}
a:hover {
  color: var(--color-white);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-light);
  padding: 0.75rem 1.2rem;
  border-radius: 0.2rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--o-transition);
}
.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* ========== medium devices ====== */
@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }
  section {
    margin-top: 6rem;
  }
}

/* ========== small devices =========== */
@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }
  section > h2 {
    margin-bottom: 2rem;
  }
}
